.information-city__wrapper {
  box-sizing: border-box;
  padding-top: 43px;
}

.information-city__headline {
  line-height: 26px;
  @include font(18px, 700, black, center);
  text-transform: uppercase;
  margin: 0;
}

.information-city__text,
.best-offers__text {
  @include font(14px, 400, #333333, center);
  line-height: 26px;
  text-transform: uppercase;
}

.information-city__text {
  margin-top: 26px;
  margin-bottom: 27px;
}

.information-city__wrapper {
  background-color: white;
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: content-box;
}
