.features__headline {
  margin-left: auto;
  margin-right: auto;
  width: 214px;
  margin-top: 39px;
  font-family: "Myriad Pro", "Arial", "sans-serif";
  font-size: 18px;
  font-weight: 100;
  color: white;
}

.features__number {
  margin-left: auto;
  margin-right: auto;
  margin-top: 14px;
}

.features__text {
  width: 281px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 16px;
}

.features__reasons-text {
  @include size_color(320px, 191px, #81b3d2);
}

.features__wrapper {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin-left: auto;
  margin-right: auto;
}

.features__reasons {
  @include size_background(320px, 260px, "../img/hill_1x.png");
}
